.body-content {
    width: calc(100vw - 18%);
    float: right;
    position: relative;
    top: 56px;
}

.content-area {
    padding: 40px 60px;
    // height: calc(100vh - 54px);
    // max-height: 100vh;

}

.content-area-fullwidth {
    width: 100%;
    float: left;
    padding: 40px;
    height: calc(100vh - 54px);
    max-height: 100vh;
}

.content-area,
.content-area-fullwidth {

    .heading,
    .main-heading {
        font-size: 24px;
        font-weight: 500;
    }

    p {
        font-size: 13px;
    }

    .sub-heading {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 0px;

    }

    p {
        font-size: 13px;
        margin-top: 4px;
        margin-bottom: 30px;
    }

    section {
        padding: 1rem 0 2rem;

    }
}



.content-wrapper .content-area {
    grid-area: content;


    section {
        width: 100%;
    }
}

.content-wrapper {
    display: grid;
    grid-template-rows: auto auto 1fr;
    grid-template-columns: minmax(25rem, 75rem) 1fr;
    grid-template-areas: 'content nav';
    width: 100%;
    margin-left: 0;
}

.bookmark-navigator {
    width: 160px;
    float: left;
    padding: 20px;
    border-left: 1px solid #eee;
    height: 80vh;
    grid-area: nav;
    position: sticky;
    top: 56px;


    h6 {
        margin-top: 0px;
        margin-bottom: 10px;
        font-size: 13px;
        font-weight: 600;
        color: #656d7a;
    }
}

.bookmark-navigator ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: sticky;
    inset-block-start: 80px;
}

.bookmark-navigator ul li a {
    font-size: 13px;
    color: #333;
    text-decoration: none;
    line-height: 32px;
    transition: all 0.3s ease-in-out;
}

.bookmark-navigator ul li.active:before {
    border-left: 2px solid #0c66e4;
    content: "";
    position: absolute;
    width: 2px;
    height: 30px;
    left: -21px;
    transition: all 0.3s ease-in-out;
}

.bookmark-navigator ul li.active a {
    color: #0c66e4;
    font-weight: 500;
    transition: all 0.3s ease-in-out;
}

.text>code {
    line-height: 1.5;
    letter-spacing: 0;
    font-weight: 400;
    -webkit-font-smoothing: subpixel-antialiased;
    padding: 2px 4px;
    color: #1C2025;
    background-color: #F3F6F9;
    border: 1px solid;
    border-color: #DAE2ED;
    border-radius: 6px;
    font-size: 0.8125rem;
    direction: ltr;
    -webkit-box-decoration-break: clone;
}

